import { debounce } from "underscore";
import $ from 'jquery';
import Swiper from 'swiper';
import { Resp } from "../modules/dev/_helpers";
import { TimelineLite, TweenLite } from "gsap";
import Mask from "jquery-mask-plugin";

/**
 * Website's common scripts (example).
 *
 * @module Common
 */

export class Common {
  /**
   * Cache data, make preparations and initialize common scripts.
   */
  constructor() {
    $("input[type='tel']").mask("+7 (999) 999-99-99");
    if (Resp.isDesk) {
      $("body").addClass("with-hover");
      $(".header__top__contacts__button").click(() => {
        this.tl.reverse();
        $(".header").addClass("fix");
      });
    }
    if (!Resp.isDesk) {
      $(".header").addClass("fixed active visible");
      $(".mobile-active").addClass("active");
      $(".products").addClass("active");
    }

    this.tl = new TimelineLite({
      paused: true,
      immediateRender: false
    });
    this.tl.set(".header", {
      y: 0
    });
    this.tl
      .to(".header", 0.4, {
        autoAlpha: 0,
        onComplete: () => {
          $(".header").addClass("fixed active visible");
        }
      })
      .to(".header", 0.5, {
        autoAlpha: 1,
        onReverseComplete: () => {
          $(".header").removeClass("fixed active visible");
        }
      });

    this.popUpAnimate = new TimelineLite({
      paused: true
    });
    this.popUpAnimate.to(".popup", 0.3, {
      autoAlpha: 1
    });

    this.popUpSucces = new TimelineLite({
      paused: true
    });
    this.popUpSucces
      .to(".popup__main", 0.3, {
        autoAlpha: 0
      })
      .to(".popup__content__send", 0.3, {
        autoAlpha: 1
      });

    this.scrollTop = 0;
    // initialize after construction
    this.init();
  }

  throttle = (func, wait) => {
    let context, args, timeout, throttling, more, result;
    const whenDone = debounce(() => {
      more = throttling = false;
    }, wait);
    return function() {
      context = this;
      args = arguments;
      var later = function() {
        timeout = null;
        if (more) func.apply(context, args);
        whenDone();
      };
      if (!timeout) timeout = setTimeout(later, wait);
      if (throttling) {
        more = true;
      } else {
        result = func.apply(context, args);
      }
      whenDone();
      throttling = true;
      return result;
    };
  };

  validateForm(container) {
    $(".popup__content__form__label").removeClass("error");
    $(".iferror").hide();
    $(".ifok").hide();
    const pattern = /^([a-z0-9_\.-])+@[a-z0-9-]+\.([a-z]{2,4}\.)?[a-z]{2,4}$/i;
    let errors = 1;
    const site = $(container).find(".site-f");
    const name = $(container).find(".name-f");
    const email = $(container).find(".mail-f");
    const phone = $(container).find(".phone-f");

    if (site.val().length < 2) {
      errors = 0;
      site.parent().addClass("error");
    }

    if (name.val().length < 2) {
      errors = 0;
      name.parent().addClass("error");
    }

    if (!pattern.test(email.val())) {
      errors = 0;
      email.parent().addClass("error");
    }

    if (phone.val() < 18) {
      errors = 0;
      phone.parent().addClass("error");
    }
    if (errors == 1) {
      $.ajax({
        url: "/local/ajax/send.php",
        type: "POST",
        dataType: "html",
        data: $("#CPAFORM").serialize(),
        success: function(response) {
          $(".ifok").show();
          $("#CPAFORM")[0].reset();
          ga("send", "event", "rate_agency", "reg");
          this.popUpSucces.play();
        },
        error: function(response) {}
      });
    } else {
      $(".iferror").show();
    }
  }

  popUpActions = () => {
    const popUpTrigger = $(".popup__trigger");
    const popUpContainer = $(".popup");
    const popUpClose = $(".popup__content__close");
    const popUpSubmit = $(".popup__content__form__button");

    popUpTrigger.click(() => {
      this.popUpAnimate.play();
      $("body").addClass("hide");
      $("html").addClass("hide");
    });
    popUpClose.click(() => {
      this.popUpAnimate.reverse();
      this.popUpSucces.reverse();
      $("body").removeClass("hide");
      $("html").removeClass("hide");
    });

    popUpSubmit.click( (e) => {
      e.preventDefault();
      const _this = $(e.currentTarget);
      const parentForm =  $(_this).parent('.popup__content__form');
      console.log(parentForm);
      this.validateForm(parentForm);
    });
  }
  mobileActions() {
    const menuOpenBtn = $(".header__top__burger");
    const menuContainer = $(".header__bottom");
    const menuChildOpen = $(".header__menu__link.has-active");

    const toggleMenu = () => {
      menuOpenBtn.toggleClass("active");
      menuContainer.toggleClass("active");
      $("body").toggleClass("hide");
      $("html").toggleClass("hide");
      if (!menuOpenBtn.hasClass("active")) {
        $(".sub__menu").removeClass("active");
        $(".header__menu__link").removeClass("active");
        $(".mobile-active").addClass("active");
        $(".products").addClass("active");
      }
    };

    menuOpenBtn.click(function() {
      toggleMenu();
    });

    menuChildOpen.click(function() {
      if (
        !$(this)
          .siblings(".sub__menu")
          .hasClass("active")
      ) {
        $(".header__menu__link").removeClass("active");
        $(".sub__menu").removeClass("active");
        $(this)
          .siblings(".sub__menu")
          .addClass("active");
        $(this).addClass("active");
      } else {
        $(".sub__menu").removeClass("active");
        $(this).removeClass("active");
      }
    });

    const phoneBtn = $(".header__top__contacts__button");

    phoneBtn.click(function() {
      menuOpenBtn.removeClass("active");
      menuContainer.removeClass("active");
      $(".sub__menu").removeClass("active");
      $(".header__menu__link").removeClass("active");
      $(".mobile-active").addClass("active");
      $(".products").addClass("active");
      $("body").removeClass("hide");
      $("html").removeClass("hide");
      $(this)
        .siblings(".header__top__contacts__numbers")
        .toggleClass("active");
    });

    const $win = $(window); // or $box parent container
    const $box = $(".header__top__contacts__numbers");

    $win.on("click.Bst", function(event) {
      if (
        $box.has(event.target).length == 0 &&
        !$box.is(event.target) &&
        (phoneBtn.has(event.target).length == 0 && !phoneBtn.is(event.target))
      ) {
        $box.removeClass("active");
      }
    });
  }
  homeTabs() {
     $('ul.tabs li').click(function(){
         var tab_id = $(this).attr('data-tab');
         $('ul.tabs li').removeClass('current');
         $('.tab-content').removeClass('current');
         $(this).addClass('current');
         $("#"+tab_id).addClass('current');
     });
  }
  headerActivate = e => {
    const header = $(".header");
    const headerContainer = header.find(".container");
    const headerHeight = 300;
    const currentScrollTop = window.pageYOffset;

    if (currentScrollTop > 140) {
      if (!header.hasClass("active")) this.tl.play();
    } else {
      this.tl.reverse();
      $(".header").removeClass("fix");
    }
    $(".header__top__contacts__numbers").removeClass("active");
  };

  sliderReports() {
    let sliderSlide = $('#calltracking__reports__slider');
    let sliderText = $('#calltracking__reports__quote');
    if (sliderText.length && sliderSlide.length) {
      const swiperSlider = new Swiper (sliderSlide, {
          effect: "slide",
          slidesPerView: 1,
          loop: true,
          // Navigation arrows
          pagination: {
              el: '.swiper-pagination',
              type: 'bullets',
          },
          navigation: {
              nextEl: '.swiper-button-next',
              prevEl: '.swiper-button-prev',
          }
      });
      const swiperQuote = new Swiper (sliderText, {
          effect: "slide",
          slidesPerView: 1,
          loop: true,
          // Navigation arrows
      });
      swiperSlider.controller.control = swiperQuote;
      swiperQuote.controller.control = swiperSlider;
    }

  };

  sliderCases() {
      const swiperCase = new Swiper ('#calltracking__cases__slider', {
          effect: "slide",
          slidesPerView: 3,
          spaceBetween: 16,
          allowSlideNext: false,
          allowSlidePrev: false,
          breakpoints: {
              // when window width is <= 960px
              960: {
                  direction: 'vertical',
                  spaceBetween: 24,
              },
              768: {
                  direction: 'horizontal',
                  slidesPerView: 1,
                  spaceBetween: 16,
                  allowSlideNext: true,
                  allowSlidePrev: true,
              },
          },
          on: {
              init: function () {
                  $('#brands__index').text(this.activeIndex + 1);
                  $('#brands__count').text(this.slides.length);
              },
              slideChange: function () {
                  $('#brands__index').text(this.activeIndex + 1);
              },
          }
      })
  };

  sliderAwards() {
      const swiperAwards = new Swiper ('#home-awards__slider', {
          effect: "slide",
          slidesPerView: 5,
          allowSlideNext: false,
          allowSlidePrev: false,
          breakpoints: {
              1200: {
                  spaceBetween: 24,
              },
              960: {
                  spaceBetween: 16,
              },
              // when window width is <= 960px
              768: {
                  direction: 'horizontal',
                  slidesPerView: 1,
                  allowSlideNext: true,
                  allowSlidePrev: true,
              },
          },
      })
  };

  playVideo() {
      const showMovie = () => {
         $("#home-show__movie").css("display", "block");
         $("#home-show__movie")[0].src += "&autoplay=1";
      };
      $('#home-show__play').on('click', () => $("#home-show__container").fadeOut(1000, showMovie));
  }

  showMore() {
    const moreButton = $(".albato__more");
    const showIntegrations = $(".show");
    moreButton.click(() =>  {
      showIntegrations.toggleClass("show");
      });
  };
  /**
   * Initialize common scripts.
   */
  init() {
    this.sliderReports();
    this.showMore();
    this.homeTabs();
    this.popUpActions();
    this.sliderCases();
    this.sliderAwards();
    this.playVideo();
    if (!Resp.isMobile) {
      $(document).scroll(this.throttle(this.headerActivate, 100));
    }

    if (Resp.isMobile) this.mobileActions();
  }
}

/** Export initialized common scripts by default */
export default new Common();



